import * as React from 'react'

import { ButtonGroup, Button, Typography } from '@material-ui/core'
import { useApolloQuery } from '@src/queries/__generated__/AcceptPrediction.hooks'
import { GetTransactions_transactions_prediction } from '@src/queries/__generated__/GetTransactions.hooks'

interface SmartCatProp {
  prediction: GetTransactions_transactions_prediction
  transactionId: number
  openCategoryDialog: () => void
}
const isWide = window.innerWidth > 600
export const SmartCatField: React.FC<SmartCatProp> = (props) => {
  const { prediction, transactionId, openCategoryDialog } = props
  const [acceptPrediction, { loading, error }] = useApolloQuery({
    variables: { id: transactionId },
  })
  const split = prediction.split ? 'Split ' : ''
  if (error) return <div>{error.message}</div>
  if (loading) return <div>saving...</div>
  return (
    <div>
      <Typography
        variant={isWide ? 'body1' : 'caption'}
      >{`${split}${prediction.categoryName}?`}</Typography>
      <ButtonGroup color="primary" aria-label="outlined primary button group">
        <Button onClick={(e) => acceptPrediction()} variant="contained">
          YES
        </Button>
        <Button onClick={(e) => openCategoryDialog()} variant="contained" color="secondary">
          NO
        </Button>
      </ButtonGroup>
    </div>
  )
}
