import * as React from 'react'
import { List, ListItem, ListItemText } from '@material-ui/core'
import { Table } from './Table'
import { getPathName, pushHistory } from '@src/history'
import { UrlPathEnum } from '@src/types'
import { useApolloQuery } from '@src/queries/__generated__/GetTableNames.hooks'

export const TablesPage = () => {
  const { loading, data } = useApolloQuery()
  if (loading || !data) return <div>loading ...</div>

  const paths = getPathName().split('/')
  if (paths[2]) {
    const t = data.tables.find((t) => t.name === paths[2])
    if (!t) {
      throw new Error(paths[2])
    }
    return <Table name={t.name} fields={t.fields.map((c) => c.name)} />
  }

  return (
    <List>
      {data.tables.map((t) => {
        return (
          <ListItem
            key={t.name}
            button
            onClick={() => pushHistory((UrlPathEnum.TablesPage + `/${t.name}`) as any)}
          >
            <ListItemText primary={t.name} />
          </ListItem>
        )
      })}
    </List>
  )
}
