import * as React from 'react'
import {
  TableBody,
  TableHead,
  Icon,
  IconButton,
  Tooltip,
  TableRow,
  TableContainer,
  Table as T,
  TableCell,
} from '@material-ui/core'
import { useApolloQuery } from '@src/queries/__generated__/GetRecords.hooks.ts'
import { pushHistory } from '@src/history'
import { UrlPathEnum } from '@src/types'

interface ITableProps {
  name: string
  fields: Array<string>
}

const Cell: React.FunctionComponent<{ value: string }> = ({ value }) => {
  const content = <TableCell>{value ? value.substring(0, 100) : ''}</TableCell>
  if (value.length > 100) {
    return <Tooltip title={value}>{content}</Tooltip>
  }
  return content
}

export const Table: React.FunctionComponent<ITableProps> = ({ fields, name }) => {
  const { loading, data } = useApolloQuery({
    variables: {
      filters: {
        recordType: name,
        offset: 0,
        limit: 100,
      },
    },
  })
  if (loading || !data) return <div>loading...</div>

  return (
    <div>
      <IconButton onClick={(e) => pushHistory(UrlPathEnum.TablesPage)}>
        <Icon fontSize="large" color="primary">
          arrow_back
        </Icon>
      </IconButton>
      <TableContainer>
        <T>
          <TableHead>
            <TableRow>
              {fields.map((c) => (
                <TableCell key={c}>{c}</TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {data.records.map((r, index) => (
              <TableRow key={index}>
                {r.nameValuePairs.map((pair, i) => (
                  <Cell key={pair.name} value={pair.value}>
                    {' '}
                  </Cell>
                ))}
              </TableRow>
            ))}
          </TableBody>
        </T>
      </TableContainer>
    </div>
  )
}
