import * as React from 'react'
import {
  DialogContentText,
  DialogTitle,
  Icon,
  InputLabel,
  IconButton,
  FormControl,
  MenuItem,
  Button,
  Checkbox,
  FormControlLabel,
  TextField,
  Select,
  Dialog,
  DialogActions,
  DialogContent,
} from '@material-ui/core'
import { UrlStateContext } from '@src/Root/contexts'
import { UrlPathEnum } from '@src/types'
import { pushHistory } from '@src/history'

interface Prop {
  categoryChoices: Array<{ id: number; label: string }>
}

export const FilterDialog: React.FC<Prop> = ({ categoryChoices }) => {
  const urlFilters = React.useContext(UrlStateContext)
  const [open, setOpen] = React.useState(false)
  const [eventId, setEventId] = React.useState(urlFilters.eventId || '')
  const [sharedOnly, setSharedOnly] = React.useState(urlFilters.sharedOnly || false)
  const [uncategorizedOnly, setUncategorizedOnly] = React.useState(
    urlFilters.uncategorizedOnly || false,
  )
  const [searchText, setSearchText] = React.useState<string>(urlFilters.searchText || '')
  const [categoryId, setCategoryId] = React.useState<number | null>(urlFilters.categoryId || null)

  const clearFilters = () => {
    setEventId('')
    setSharedOnly(false)
    setUncategorizedOnly(false)
    setSearchText('')
    setCategoryId(null)
  }
  const resetFilters = () => {
    console.log('reset filters')
    setEventId(urlFilters.eventId || '')
    setSharedOnly(urlFilters.sharedOnly || false)
    setSearchText(urlFilters.searchText || '')
    setCategoryId(urlFilters.categoryId || null)
    setUncategorizedOnly(urlFilters.uncategorizedOnly || false)
  }

  const handleClickOpen = () => {
    resetFilters()
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  if (!open) {
    return (
      <IconButton onClick={handleClickOpen}>
        <Icon color="primary">filter_alt</Icon>
      </IconButton>
    )
  }
  return (
    <div>
      <IconButton onClick={handleClickOpen}>
        <Icon color="primary">filter_alt</Icon>
      </IconButton>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Filter Transactions</DialogTitle>
        {open ? (
          <DialogContent>
            <DialogContentText>
              Search transactions by category, text (merchant name and notes). By checking "Shared",
              you can limit the results to be shared transactions only.
            </DialogContentText>
            <FormControl style={{ minWidth: 120 }}>
              <InputLabel>Category</InputLabel>
              <Select
                id="categoryId"
                value={categoryId || ''}
                onChange={(e) => setCategoryId(parseInt(e.target.value as string))}
              >
                <MenuItem value={''}>None</MenuItem>
                {categoryChoices.map((choice) => (
                  <MenuItem key={choice.id} value={choice.id}>
                    {choice.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <TextField
              margin="dense"
              id="eventId"
              label="Event"
              type="string"
              fullWidth
              value={eventId || ''}
              onChange={(e) => setEventId(e.target.value)}
            />
            <TextField
              onChange={(e) => setSearchText(e.target.value)}
              value={searchText || ''}
              margin="dense"
              id="searchText"
              label="Text Search"
              type="text"
              fullWidth
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={sharedOnly}
                  onChange={(e) => setSharedOnly(e.target.checked)}
                  name="sharedOnly"
                />
              }
              label="Shared"
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={uncategorizedOnly}
                  onChange={(e) => setUncategorizedOnly(e.target.checked)}
                  name="uncategorizedOnly"
                />
              }
              label="Uncategorized"
            />
          </DialogContent>
        ) : null}
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={clearFilters} color="primary">
            Reset
          </Button>
          <Button
            onClick={(e) => {
              pushHistory(UrlPathEnum.TransactionList, {
                eventId,
                searchText,
                sharedOnly,
                categoryId,
                uncategorizedOnly,
              })
              handleClose()
            }}
            color="primary"
          >
            Apply
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
